import React from 'react'
import { Jumbotron, Container } from 'reactstrap'

const Jumbo = () => {
    return(
        <div>
            <Jumbotron fluid>
            <Container fluid>
                <h1 className="display-3">Hey Crazy!!!</h1>
                <p className="lead">This is a simple jumbotron present on a reactstrap.</p>
            </Container>
            </Jumbotron>
      </div>
    )
}
export default Jumbo