import React, { useState } from 'react';
import Jumbo from './Jumbo'
import Footer from './Foot'
import { Container, Col, Row, Button, Form, FormGroup, Label, Input } from 'reactstrap'

function App() {

  const datas = {
    company: "DevPro",
    address: "CL ProMorar",
    devs: [
      "Node.js", "gatsby", "NextJS", "React"
    ]
  }

  
  const [email, setEmail] = useState('')
  const [password, setPassword] = useState('')

  const handleChangeEmail = (event) => {
    setEmail(event.target.value)
  }
  const handleChangePassword = (event) => {
    setPassword(event.target.value)
  }
  const handleSubmit = (event) => {
    alert(`Hello ${email} your pwd is: ${password}`)
    event.preventDefault()
    event.target.email.value = ''
    event.target.password.value = ''
  }
  
  return (
    <Container>
      <Jumbo />
      <div>
        <Form onSubmit={handleSubmit}>
          <Row form>
            <Col md={6}>
              <FormGroup>
                <Label for="email">Email</Label>
                <Input type="text" name="email" id="email" placeholder="Enter you the best email" onChange={handleChangeEmail} />
              </FormGroup>
            </Col>
            <Col md={6}>
              <FormGroup>
                <Label for="password">Password</Label>
                <Input type="text" name="password" id="password" placeholder="Enter your password" onChange={handleChangePassword} />
              </FormGroup>
            </Col>
          </Row>
          <Button type="submit">Sign in</Button>
        </Form>
      </div>
      <Footer data = { datas } />
    </Container>
  )
}

export default App
