import React from 'react'
import { Container, Badge, Row, Col } from 'reactstrap'
import img from './images/leprechau.png'

const Footer = (props) => {
    return(
        <Container className="mt-4">
            <Row className="bg-light">
                <Col xs="6">
                <span className="text-dark">Developer by {props.data.company} our address {props.data.address} and us the best in</span>
                     <ul>
                        {props.data.devs.map((dev) =><li key={dev}><Badge href="#" color="info">{dev}</Badge></li> )}
                    </ul>
                </Col>
                <Col xs="6" className="align-self-center">
                    <img src={img} className="img-fluid float-right" width="50" height="50" alt="simbol" />
                </Col>
            </Row>
        </Container>
    )
}
export default Footer